<template>
  <div class="loginbck">
    <div class="login">
      <div style="font-weight: 700; font-size: 30rem"></div>
      <div class="img">
        <img src="../assets/img/logo2.png" />
      </div>

      <div class="imputbox">
        <input
          v-model="usname"
          type="text"
          class="input-field"
          :placeholder="placeholder.usname"
        />
        <input
          v-model="uspwd1"
          type="password"
          class="input-field"
          :placeholder="placeholder.pwd"
        />
        <input
          v-model="uspwd2"
          type="password"
          class="input-field"
          :class="{ zhuce: zhuce }"
          :placeholder="placeholder.zhuce"
          ref="zhuce"
        />
      </div>

      <div class="btnbox">
        <button class="bt1" @click="login" :class="{ btnal: zhuce }">
          
        </button>
        <button @click="zhucemtd" class="bt2" :class="{ btnal: !zhuce }">
          {{ placeholder.zhuce }}
        </button>
      </div>
      <div></div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

import { Toast } from "vant";
export default {
  data() {
    return {
      zhuce: true,
      placeholder: {
        usname: "",
        pwd: "",
        zhuce: "",
      },

      //   
      usname: "",
      uspwd1: "",
      uspwd2: "",

      ueseinfo: JSON.parse(localStorage.getItem("userInfo")) || null,
    };
  },
  computed: {
    Token() {
      return this.$store.state.uese;
    },
  },
  methods: {
    ...mapMutations[("pushuserArr", "login")],

    login() {
      if (this.ueseinfo) {
        if (!this.zhuce) {
          this.placeholder = {
            usname: "",
            pwd: "",
            zhuce: "",
          };
          this.usname = "";
          this.uspwd1 = "";
          this.uspwd2 = "";

          this.zhuce = !this.zhuce;
        } else {
          let ssd = true;
          if (this.ueseinfo) {
            let ttr = this.ueseinfo.find((e) => e.usname == this.usname);

            if (ttr) {
              ssd = ttr.uspwd == this.uspwd1;
            } else {
              ssd = false;
            }
          } else {
            ssd = false;
          }

          if (ssd) {
            // this.ueseinfo
            this.$store.commit("login", {
              usname: this.usname,
              uspwd: this.uspwd1,
            });

            console.log(this.Token);

            // if (this.Token) {
            Toast(`!${this.Token.usname}`);
            this.$router.replace(this.$route.query.target);

            this.usname = "";
            this.uspwd1 = "";
            this.uspwd2 = "";
            // } else {
            // }
            // console.log(found, tk);

            console.log("");
          } else {
            Toast("");
          }
        }
      } else {
        this.zhuce = false;
        Toast("");
      }
    },
    zhucemtd() {
      const regex = /^[A-Za-z0-9]+$/;

      if (this.zhuce) {
        this.placeholder = {
          usname: "",
          pwd: "",
          zhuce: "",
        };
        this.usname = "";
        this.uspwd1 = "";
        this.uspwd2 = "";
        this.zhuce = !this.zhuce;
      } else {
        if (regex.test(this.usname)) {
          if (this.usname && this.uspwd1 && this.uspwd2) {
            if (this.uspwd1 === this.uspwd2) {
              let ssd = true;
              if (this.ueseinfo) {
                ssd = this.ueseinfo.find((e) => e.usname == this.usname);
                console.log("", this.ueseinfo);
              } else {
                ssd = false;
              }

              if (ssd) {
                Toast("");
              } else {
                this.$store.commit("pushuserArr", {
                  usname: this.usname,
                  uspwd: this.uspwd1,
                });

                console.log(`${btoa(this.usname)}Mijiao${this.uspwd1}`);

                (this.ueseinfo =
                  JSON.parse(localStorage.getItem("userInfo")) || null),
                  Toast("!");

                this.zhuce = !this.zhuce;
                this.placeholder = {
                  usname: "",
                  pwd: "",
                  zhuce: "",
                };

                // this.usname = "";
                // this.uspwd1 = "";
                this.uspwd2 = "";
                console.log(
                  "",
                  this.usname,
                  this.uspwd1,
                  this.uspwd2
                );
              }
            } else {
              Toast("");
            }
          }
        } else {
          Toast("");
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.loginbck {
  color: aliceblue;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-flow: column;
  justify-content: space-evenly;
  background: url("@/assets/img/logbck2.png") center center no-repeat;
  background-size: auto 100vh;
}

.login {
  height: 100vh;
  width: 100vw;

  background-color: rgba(255, 255, 255, 0.1); /*  */
  backdrop-filter: blur(2px); /*  */

  display: flex;
  align-items: center;
  flex-flow: column;
  justify-content: space-evenly;

  .img {
    width: 60rem;
    height: 60rem;
    background-color: rgba(255, 255, 255, 0.1); /*  */
    overflow: hidden;
    border-radius: 20rem;
    img {
      transform: translate(-15rem, -14rem);

      height: 90rem;
      width: 90rem;
    }
  }
}

// 
.input-field {
  width: 50vw;
  padding: 15px 20px; /*  */
  margin: 10px 0; /*  */
  border: none; /*  */
  background-color: rgba(255, 255, 255, 0.5); /*  */
  color: #000000; /*  */
  font-size: 16px; /*  */
  outline: none; /*  */
  border-radius: 25px; /*  */
  backdrop-filter: blur(10px); /*  */
  transition: all 0.3s ease; /*  */

  transform: translate(0);

  &:focus {
    background-color: rgba(255, 255, 255, 1); /*  */
    width: 60vw;
  }

  &::placeholder {
    color: #5f5f5f; /*  */
  }

  margin: 10rem;
}

.btnbox {
  width: 70vw;
  height: 50rem;
  display: flex;
  button {
    height: 50rem;
    flex-grow: 1;
    border: none;
    background-color: rgba(255, 255, 255, 0.2); /*  */
    backdrop-filter: blur(2px); /*  */

    transition: all 0.5s ease;
  }
  .bt1 {
    width: 52rem;
    border-radius: 25rem 0 0 25rem;
  }
  .bt2 {
    width: 52rem;
    overflow: hidden;
    white-space: nowrap;

    border-radius: 0 25rem 25rem 0;
  }

  .btnal {
    flex-grow: 8;
    overflow: hidden;
    white-space: nowrap;
    background-color: rgba(255, 255, 255, 0.8); /*  */
    color: #5f5f5f;
  }
}

.imputbox {
  width: 80vw;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.zhuce {
  transform: translateX(-200%);
}
</style>